.streams-page {
  &__head {
    &-list {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      margin: 0 -7px;
      margin-top: 14px;
    }
    &-item {
      display: flex;
      margin: 7px;
      width: calc(25% - 14px);
      border-radius: $borderRadius4;
      overflow: hidden;
      &:not(:nth-child(n+4)) {
        width: calc(33.333% - 14px);
      }
    }
    &-link {
      display: flex;
      position: relative;
      width: 100%;
      &:hover .streams-page__head-descr {
        display: flex;
      }
    }
    &-img {
      width: 100%;
    }
    &-star,
    &-viewer,
    &-name,
    &-descr {
      position: absolute;
    }
    &-star {
      top: 10px;
      left: 10px;
    }
    &-viewer {
      top: 10px;
      right: 10px;
      color: $white;
      img {
        margin-right: 8px;
      }
    }
    &-name {
      bottom: 10px;
      left: 10px;
      color: $white;
      img {
        margin-right: 8px;
      }
    }
    &-descr {
      display: none;
      width: 100%;
      height: 100%;
      padding: 10px;
      align-items: center;
      justify-content: center;
      text-align: center;
      font-size: 13px;
      color: $white;
      background: rgba($black, .3);
    }
  }
  &__list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin: 0 -7px;
  }
  &__item {
    display: flex;
    width: calc(50% - 14px);
    margin: 7px;
  }
  &__link {
    display: flex;
    align-items: flex-start;
    padding: 10px;
    background: $black2;
    &:hover {
      background: $black3;
    }
  }
  &__img {
    width: 130px;
    margin-right: 10px;
  }
  &__wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  &__name,
  &__viewer {
    font-size: 13px;
    img {
      margin-right: 4px;
    }
  }
  &__descr {
    width: 100%;
    font-size: 13px;
    margin-top: 8px;
    max-height: 60px;
  }
}

@media (max-width: 750px) {
  .streams-page {
    &__head-list {
      display: none;
    }
    &__item,
    &__link,
    &__wrap {
      width: 100%
    }
  }
}