.shop-product {
  &__descr {
    margin-top: 24px;
    margin-bottom: 24px;
  }
  &__product {
    display: flex;
    flex-direction: column;
    align-items: center;
    float: right;
    margin-left: 20px;
    margin-bottom: 15px;
    max-width: 340px;
    &-img {
      display: flex;
      align-items: center;
      justify-content: center;
      img {
        width: 100%;
        margin: 0;
      }
    }
    &-price {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-wrap: wrap;
      color: $green;
      font-size: 18px;
      margin: 0 5px;
      span {
        margin: 0 5px;
        font-size: 14px;
        color: $grey2;
        text-decoration: line-through;
      }
    }
    &-btn {
      color: $grey1;
      font-size: 15px;
      text-align: center;
      height: auto;
      padding: 5px 15px;
      background: $black0;
      &:hover {
        background: $red;
      }
    }
    & > span {
      font-size: 12px;
      margin-top: 3px;
    }
  }
  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 24px;
    & > span {
      font-size: 12px;
      margin-bottom: 10px;
    }
  }
  &__list {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin: 0 -5px;
    margin-bottom: 20px;
  }
  &__item {
    margin: 5px;
    a {
      display: flex;
      align-items: center;
      justify-content: center;
      &:hover {
        opacity: 0.8;
      }
      img {
        max-width: 196px
      }
    }
  }
}

@media (max-width: 1202px) {
  .shop-product {
    &__list {
      justify-content: center;
    }
  }
}

@media (max-width: 700px) {
  .shop-product {
    & .shop-page__item:last-child {
      display: none;
    }
  }
}

@media (max-width: 600px) {
  .shop-product {
    &__product {
      float: none;
      margin: auto;
      margin-bottom: 15px;
    }
  }
}