.global-content {
  h2,
  h3  {
    font-weight: 500;
    font-size: 20px;
    line-height: 140%;
    letter-spacing: 0.15px;
    color: $grey0;
    margin: 0;
    margin-bottom: 20px;
  }
  h3 {
    font-size: 17px;
    line-height: 135%;
    letter-spacing: 0.25px;
    margin-bottom: 8px;
  }
  p {
    margin-bottom: 8px;
    img {
      vertical-align: middle;
      margin: 0 4px;
    }
    a:hover img{
      opacity: .8;
    }
  }
  p > a {
    color: $grey2;
    text-decoration: underline;
    &:hover {
      color: $white;
      text-decoration: none;
    }
  }
  & img {
    max-width: 100%;
  }
  &>ul {
    list-style: disc outside none;
  }
  ol li::marker {
    color: $grey2;
  }
  ol,
  ul {
    margin: 0 0 0 15px; 
    padding: 0;
    margin-bottom: 32px;
    li {
      display: list-item;
      margin-bottom: 4px;
      &:last-child {
        margin-bottom: 0;
      }
      & > div {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
      }
      a {
        color: $grey2;
        text-decoration: underline;
        &:hover {
          color: $white;
          text-decoration: none;
        }
      }
      span,
      a {
        display: flex;
        align-items: center;
        margin-right: 8px;
        min-height: 23px;
        img {
          display: flex;
          width: 16px;
          margin: 0;
          margin-right: 4px;
        }
      }
    }
  }
  blockquote {
    position: relative;
    padding: 28px;
    padding-left: 80px;
    margin: 0;
    margin-bottom: 24px;
    background: $gradientHover;
    border-radius: $borderRadius4;
    &::before {
      content: '';
      position: absolute;
      top: 28px;
      left: 28px;
      width: 40px;
      height: 28px;
      background: url(../img/news-news/quote.svg) no-repeat center;
    }
  }
  &__quote {
    padding: 28px;
    margin: 0;
    background: $gradientHover;
    border-radius: $borderRadius4;
    & > div {
      display: flex;
      align-items: flex-start;
      p {
        font-size: 17px;
        margin-bottom: 15px;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
    img {
      width: 40px;
      margin-right: 12px;
    }
    & > div:last-child {
      display: flex;
      align-items: flex-start;
      margin-top: 16px;
      color: $grey2;
      p {
        font-size: 13px;
      }
    }
  }
  &__interview {
    & > p {
      font-size: 13px;
      letter-spacing: 0.25px;
      color: $grey2;
    }
    dl {
      margin: 0;
      margin-top: 20px;
      dt,
      dd {
        position: relative;
        margin-left: 0;
        padding-left: 24px;
        &:before {
          content: '';
          position: absolute;
          top: 11px;
          left: 0;
          width: 16px;
          height: 1px;
          background: $grey3;
        }
      }
      dt {
        margin-bottom: 8px;
        p {
          font-weight: 500;
          font-size: 17px;
          line-height: 135%;
          letter-spacing: 0.25px;
          color: $grey0;
        }
      }
      dd {
        margin-bottom: 20px;
        &:last-child {
          margin-bottom: 0;
        }
        p {
          margin-bottom: 16px;
          &:last-child {
            margin-bottom: 0;
          }
        }
      }
    }
  }
  &__tabel {
    p {
      font-size: 13px;
      letter-spacing: 0.25px;
      color: $grey2;
      margin-top: 20px;
      margin-bottom: 12px;
    }
    &-version-1,
    &-version-2 {
      background: $black2;
      width: 100%;
      border-collapse: collapse;
      margin-bottom: 24px;
      th,
      td {
        border: 1px solid $black1;
      }
    }
    &-version-1 {
      th,
      td {
        height: 44px;
        text-align: center;
        a:hover {
          color: $white;
          svg path {
            fill: $white;
          }
        }
      }
      th {
        background: $black3;
        font-weight: bold;
      }
      td > div {
        padding: 10px;
      }
      td img {
        max-width: 100px;
        max-height: 40px;
      }
      td a,
      td > div {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        img {
          border-radius: $borderRadius4;
        }
        span {
          display: flex;
          align-items: center;
          justify-content: center;
          margin: 8px;
          img {
            margin-right: 8px;
          }
        }
      }
      tr:hover {
        background: $black3;
      }
      &--cybersport {
        font-size: 13px;
        th {
          height: auto;
          padding-top: 5px;
          padding-bottom: 5px;
        }
        th,
        td {
          border: none;
        }
        th,
        th a {
          background: $black1;
          font-size: 13px;
          color: $grey3;
        }
        th:first-child {
          text-align: left;
          padding-left: 15px;
        }
        td:first-child div {
          justify-content: flex-start;
          img:nth-child(2) {
            margin-left: 10px;
          }
        }
        &__logo {
          width: 56px;
          height: 56px;
          max-width: 56px !important;
          max-height: 56px !important;
        }
        .global-content__tabel--right {
          justify-content: flex-end;
          a {
            position: relative;
            &:before {
              content: '';
              position: absolute;
              width: 100%;
              height: 100%;
              background: $black1;
              opacity: 0.6;
            }
            &:hover:before {
              display: none;
            }
          }
          img {
            width: 50px;
          }
        }
        .global-content__tabel--left {
          flex-direction: column;
          align-items: flex-start;
          justify-content: flex-start;
          text-align: left;
          padding-left: 5px;
          span {
            margin: 0;
            color: $grey2;
          }
        }
        .global-content__tabel--item {
          justify-content: flex-end;
          img {
            width: 27px;
          }
          &>div {
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            background: $black1;
            width: 27px;
            height: 20px;
            cursor: pointer;
            span {
              margin: 0;
            }
            div {
              display: none;
              position: absolute;
              top: 100%;
              right: 0;
              align-items: center;
              img {
              }
            }
          }
          &:hover div {
            display: flex;
          }
        }
        .global-content__tabel--90 {
          width: 70px;
          div {
            padding-right: 0;
          }
          img {
            width: 60px;
          }
          a {
            position: relative;
            span {
              position: absolute;
              display: block;
              left: 0;
              bottom: 0;
              font-size: 10px;
              padding: 2px 5px;
              margin: 0;
              background: $black3;
            }
          }
        }
      }
      &--position {
        th:first-child {
          text-align: center;
          padding-left: 0;
        }
        td:first-child div {
          justify-content: center;
        }
      }
      &_players,
      &_progress {
        a {
          position: relative;
        }
        img {
          width: 24px;
          margin: 2px;
          &:hover ~ div {
            display: block;
          }
        }
        div {
          display: none;
          position: absolute;
          top: 100%;
          left: calc(50% - 60px);
          width: 120px;
          background: $black3;
          font-size: 10px;
          text-align: left;
          z-index: 2;
        }
      }
      &_progress {
        span {
          width: 100%;
          display: inline-block;
          font-size: 10px;
          margin: 0!important;
          color: $grey2;
        }
        img {
          border-radius: $borderRadius4;
          width: 47px;
        }
      }
      &_time p {
        font-size: 14px;
        color: $grey3;
        margin: 0;
      }
      & .global-content__tabel-version-1_tournament {
        justify-content: flex-start;
      }
      &_tournament-priz {
        max-width: 110px;
      }
      &_caption p {
        font-size: 14px;
        color: $grey3;
        text-align: left;
        padding-left: 10px;
      }
      & .global-content__tabel-version-1_caption:hover {
        background: $black2;
      }
    }
    &-version-2 {
      td {
        padding: 30px;
        b {
          display: flex;
          align-items: center;
          justify-content: center;
        }
        ul {
          margin-top: 12px;
          li::marker {
            color: $grey2;
          }
          a {
            display: flex;
            align-items: center;
            color: $grey1;
            text-decoration: none;
            img {
              width: 16px;
              margin-right: 4px;
            }
          }
        }
      }
    }
  }
  &__slider {
    &-top {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 410px;
      max-height: 410px;
      background: $black0;
      border-radius: $borderRadius4 $borderRadius4 0 0;
      div {
        display: flex;
        align-items: center;
        justify-content: center;
        img {
          width: 100%;
          max-height: 410px;
        }
      }
      button {
        position: absolute;
        top: calc(50% - 20px);
        width: 40px;
        height: 40px;
        background: $black2;
        opacity: 0.7;
        border-radius: $borderRadius4;
        &:hover {
          opacity: 1;
        }
      }
    }
    &-btn--left {
      left: 20px;
    }
    &-btn--right {
      right: 20px;
      transform: rotate(180deg);
    }
    &-bottom {
      display: flex;
      align-items: flex-start;
      background: $black2;
      border-radius: 0 0 $borderRadius4 $borderRadius4;
      padding: 20px;
      span {
        margin-right: 20px;
        color: $grey3;
      }
      p {
        font-size: 13px;
      }
    }
  }
  &__poll {
    &-wrap {
      display: flex;
      justify-content: space-between;
      div {
        padding: 20px 32px;
        width: 48%;
        display: flex;
        flex-direction: column;
        background-image: $gradientMatch, url(../img/news-news/img-slider.jpg);
        background-repeat: no-repeat;
        background-size: cover;
        border-radius: $borderRadius4;
        &:last-child {
          margin-bottom: 0;
        }
        h3,
        p {
          margin: 0;
          text-align: center;
          margin: 0 auto;
        }
        p {
          font-size: 13px;
          letter-spacing: 0.25px;
          color: $grey2;
          margin-top: 12px;
        }
        label {
          display: flex;
          align-items: center;
          margin-top: 12px;
          height: 38px;
          padding: 0 12px;
          border-radius: $borderRadius4;
          background-color: $black1;
          cursor: pointer;
          font-size: 13px;
          letter-spacing: 0.25px;
          color: #C9C9CB;
          span {
            display: none;
            margin-left: 4px;
          }
          svg {
            display: none;
            margin-left: auto;
          }
          &:hover {
            background-image: $gradientHover;
            svg {
              display: block;
            }
          }
        }
        input:checked ~ label {
          color: $white;
          background-image: linear-gradient(90deg, #28282D 0%, #28282D 46%, rgba(0, 0, 0, 0) 46%, rgba(0, 0, 0, 0) 100%);
        }
        input:checked ~ label span {
          display: block;
          color: $white;
        }
        #global-content-radio-1:checked ~ label[for=global-content-radio-1],
        #global-content-radio-2:checked ~ label[for=global-content-radio-2],
        #global-content-radio-3:checked ~ label[for=global-content-radio-3],
        #global-content-radio-4:checked ~ label[for=global-content-radio-4],
        #global-content-radio-5:checked ~ label[for=global-content-radio-5],
        #global-content-radio-6:checked ~ label[for=global-content-radio-6] {
          background-image: linear-gradient(90deg, #39393E 0%, #39393E 46%, rgba(0, 0, 0, 0) 46%, rgba(0, 0, 0, 0) 100%), $gradientHover;
          svg {
            display: block;
            path {
              fill: $white;
            }
          }
        }
      }
    }
  }
  &__header,
  &__lists,
  &__quote,
  &__interview,
  &__tabel,
  &__slider {
    margin-bottom: 24px;
  }
  &__block-comment {
    background: $black1;
    border-radius: $borderRadius4;
    padding: 14px 20px;
    padding-bottom: 55px;
    &-top {
      display: flex;
      align-items: flex-start;
      padding-bottom: 14px;
      h2 {
        margin: 0;
      }
      div {
        display: flex;
        margin-left: 44px;
        a {
          margin-left: 0;
          margin-right: 12px;
          height: 28px;
          padding: 0 8px;
        }
      }
      a {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 28px;
        padding: 0 8px;
        margin-left: auto;
        font-size: 13px;
        letter-spacing: 0.25px;
        color: $grey2;
        svg {
          margin-right: 4px;
        }
        &:hover {
          color: $white;
          svg path {
            fill: $white;
          }
        }
      }
    }
  }
  &__comment {
    & > & {
      position: relative;
      padding-left: 40px;
      margin-left: 12px;
      & > a {
        position: absolute;
        top: 0;
        left: 0;
        width: 40px;
        height: 100%;
        &::before {
          content: '';
          position: absolute;
          width: 2px;
          height: 100%;
          left: 50%;
          top: 0;
          border-right: 1px dotted $grey2;
        }
        &:hover::before {
          background: $gradientRed;
          border-right: none;
        }
      }
    }
    &-message {
      display: flex;
      align-items: flex-start;
      padding: 12px;
      border-radius: $borderRadius4;
      margin-bottom: 4px;
      &--grey {
        background: $gradientHover;
      }
    }
    &-wrap {
      margin-left: 12px;
      width: 100%;
      & > b {
        position: relative;
        display: block;
        padding-left: 10px;
        font-size: 12px;
        letter-spacing: 0.3px;
        color: $grey2;
        margin: 6px 0;
        max-height: 40px;
        &:before {
          content: '';
          position: absolute;
          top: -5px;
          left: 0;
          width: 2px;
          height: calc(100% + 10px);
          background: $black4;
        }
      }
      & > p {
        margin-top: 2px;
      }
      & > a {
        font-size: 12px;
        color: $grey2;
        &:hover {
          color: $white;
        }
      }
    }
    &-block-mobile {
      display: none;
      align-items: center;
      justify-content: space-between;
      flex-wrap: wrap;
      margin-top: 5px;
      p, a , div {
        display: flex;
        align-items: center;
        font-size: 12px;
        color: $grey2;
        svg {
          margin-right: 4px;
        }
      }
      a:hover {
        color: $white;
        svg path {
          fill: $white;
        }
      }
      div {
        margin-left: auto;
        margin-right: 10px;
      }
    }
    &-user-img {
      display: flex;
      max-width: 40px;
      min-width: 40px;
      border-radius: $borderRadius50;
      img {
        width: 100%;
      }
    }
    &-name {
      font-weight: bold;
      font-size: 13px;
      margin-right: 4px;
    }
    &-reply {
      font-size: 12px;
      margin-right: 16px;
    }
    &-top {
      display: flex;
      align-items: center;
      justify-content: space-between;
      &-wrap {
        display: flex;
        align-items: center;
        p {
          display: flex;
          align-items: center;
          font-size: 13px;
          letter-spacing: 0.25px;
          color: $grey2;
          margin-right: 16px;
          svg {
            margin-right: 4px;
          }
        }
        a {
          display: flex;
          align-items: center;
          color: $grey2;
          &:hover {
            color: $white;
            svg path {
              fill: $white;
            }
          }
        }
        div {
          display: flex;
          align-items: center;
        }
      }
    }
    &-like {
      display: flex;
      align-items: center;
      a {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-right: 4px;
        &:hover svg path {
          stroke: $green;
        }
        &:last-child {
          margin-right: 0;
          margin-left: 4px;
          transform: scale(1, -1);
          &:hover svg path {
            stroke: $red;
          }
        }
      }
      span {
        font-size: 13px;
        letter-spacing: 0.25px;
        color: $grey2;
      }
      &.global-content__color-green span {
        color: $green;
      }
      &.global-content__color-red span {
        color: $red;
      }
    }
  }
  &__btn-block {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 20px;
    margin-bottom: -8px;
    a {
      margin-right: 8px;
      margin-bottom: 8px;
    }
  }
  &__btn-block-top {
    position: relative;
    display: flex;
    width: 100%;
    background: #1F1F23;
    border-radius: $borderRadius4 $borderRadius4 0 0;
    overflow: hidden;
    margin-bottom: -3px;
    font-weight: 500;
    min-width: 555px;
    a {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 17px 16px;
      &:hover {
        background: $black1;
        color: #fff;
      }
    }
    &--active {
      background: $black1;
      color: #fff;
    }
  }
  &__filter {
    display: flex;
    flex-wrap: wrap;
    margin-top: 24px;
    margin-bottom: -8px;
    input,
    select {
      height: 40px;
      background: $black2;
      border: 1px solid $black1;
      box-sizing: border-box;
      border-radius: $borderRadius4;
      padding: 0 12px;
      font-size: 13px;
      line-height: 150%;
      letter-spacing: 0.5px;
      color: $grey2;
      margin-bottom: 8px;
      margin-right: 8px;
      cursor: pointer;
      &:hover {
        background: $black3;
      }
      &::placeholder {
        color: inherit;
      }
    }
  }
  &__search {
    display: flex;
    width: 100%;
    border-radius: $borderRadius4;
    margin-top: 24px;
    margin-bottom: 24px;
    font-size: 13px;
    color: $grey1;
    line-height: 150%;
    input,
    select,
    button {
      height: 40px;
      border: none;
      color: $grey1;
      cursor: pointer;
      &:hover {
        background: $black3;
      }
      &::placeholder {
        color: $grey2;
      }
    }
    input {
      width: calc(100% - 40px);
      padding: 5px 12px;
      background: $black2;
      border-radius: $borderRadius4 0px 0px $borderRadius4;
      cursor: text;
    }
    select {
      background: $black2;
      padding: 0 12px;
      color: $grey2;
    }
    button {
      min-width: 40px;
      background: $black2;
      border-radius: 0px $borderRadius4 $borderRadius4 0px;
      &:hover svg path {
        fill: $white;
      }
    }
  }
}

@media (max-width: 900px) {
  .global-content {
    blockquote {
      padding: 15px;
      padding-left: 67px;
      &:before {
        top: 15px;
        left: 15px;
      }
    }
    &__quote {
      padding: 15px;
    }
  }
}

@media (max-width: 680px) {
  .global-content {
    blockquote {
      padding: 15px;
      &::before {
        content: '';
        position: relative;
        float: left;
        margin-top: 10px;
        margin-right: 12px;
        top: 0;
        left: 0;
      }
    }
    &__quote {
      div {
        display: block;
      }
      img {
        float: left;
        margin-top: 10px;
      }
    }
    &__tabel {
      &-version-1 {
        th,
        td {
          font-size: 12px;
        }
        td div {
          padding: 5px;
        }
        &_no-mobile {
          display: none;
        }
      }
      &-version-2 {
        td {
          display: flex;
          flex-direction: column;
          ul {
            margin-left: 0;
          }
        }
      }
    }
    &__slider {
      &-top {
        height: auto;
        &-btn--left {
          left: 10px;
        }
        &-btn--right {
          right: 10px;
        }
      }
      &-bottom {
        padding: 15px 10px;
      }
    }
    &__poll {
      &-wrap {
        flex-direction: column;
        div {
          width: 100%;
          padding: 15px 10px;
          margin-bottom: 24px;
        }
      }
    }
    &__block-comment {
      padding: 14px 15px 55px;
      &-top {
        flex-wrap: wrap;
        align-items: center;
        div {
          order: 1;
          margin: 0;
          margin-top: 10px;
          width: 100%;
        }
      }
    }
    &__comment {
      & > & {
        padding-left: 26px;
        margin-left: 0px;
        & > a {
          width: 26px;
          &::before {
            left: calc(50% - 1px);
          }
        }
      }
      &-message {
        padding-left: 6px;
        padding-right: 6px;
      }
      &-top {
        margin: 13px 0;
        &-wrap {
          .global-content__comment-reply,
          div,
          p {
            display: none;
          }
        }
      }
      &-block-mobile {
        display: flex;
      }
      &-block-mobile,
      &-wrap > b,
      &-wrap > p,
      &-wrap > a {
        margin-left: -52px;
      }
    }
  }
}
@media (max-width: 600px)  {
  .global-content {
    &__btn-block-top__wrap {
      width: 100%;
      overflow-y: hidden;
      overflow-x: scroll;
    }
    &__tabel-version-1--cybersport {
      width: calc(100% + 30px);
      margin: 0 -15px;
    }
    .shop-basket__table {
      width: calc(100% + 30px);
      margin: 0 -15px;
