.forum-section {
  & .forum-page__list {
    display: block;
    padding-left: 0;
  }
  &__list {
    margin-bottom: 24px;
    background: $black2;
    border-radius: $borderRadius4;
  }
  &__item {
    display: flex;
    align-items: center;
    padding: 11px 15px;
    border-top: 1px solid $black1;
    &--first {
      padding: 5px 15px;
      background: $black1;
      font-size: 13px;
      color: #61616f;
      border: none;
      .forum-section__col-4 {
        justify-content: flex-end;
      }
    }
    a:hover {
      color: $white;
    }
    &:nth-child(2) {
      border: none;
    }
  }
  &__table {
    margin-top: 24px;
  }
  &__col-1 {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 40px;
    max-height: 40px;
    margin-right: 15px;
    border-radius: $borderRadius50;
    overflow: hidden;
    a {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      border-radius: $borderRadius4;
    }
    img {
      width: 100%;
      border-radius: $borderRadius4;
    }
  }
  &__col-2 {
    width: 100%;
  }
  &__title {
    font-size: 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    img {
      display: block;
      margin-left: 8px;
    }
  }
  &__name {
    font-size: 12px;
    color: $grey3;
  }
  &__img-locked {
    display: flex;
    align-items: center;
    margin-left: auto;
  }
  &__mes--mobile {
    display: none;
  }
  &__col-3 {
    min-width: 160px;
    margin: 0 15px;
    p {
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-size: 13px;
    }
  }
  &__col-4 {
    min-width: 200px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    & > a {
      display: flex;
      align-items: center;
      justify-content: center;
      max-width: 40px;
      max-height: 40px;
      border-radius: $borderRadius50;
      overflow: hidden;
    }
    img {
      width: 100%;
      border-radius: $borderRadius4;
    }
    div {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      a {
        width: auto;
        font-size: 14px;
      }
      p {
        font-size: 12px;
        color: $grey3;
      }
    }
  }
}

@media (max-width: 800px) {
  .forum-section {
    &__col-3,
    &__col-4 {
      display: none;
    }
    &__mes--mobile {
      display: flex;
      justify-content: space-between;
      font-size: 12px;
      span {
        color: $grey3;
      }
    }
  }
}

@media (max-width: 600px) {
  .forum-section {
    &__list {
      margin-left: -15px;
      margin-right: -15px;
    }
  }
}

@media (max-width: 450px) {
  .forum-section {
    &__mes--mobile {
      flex-direction: column;
    }
  }
}