.news-page {
  &__content {
    .index__news-list {
      margin-top: 0;
      margin-top: 24px;
    }
    .index__news-item {
      width: 32%;
    }
  }
  &__list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 15px;
    overflow: hidden;
  }
  &__item {
    margin-right: 8px;
    margin-bottom: 8px;
    &--active a {
      background: $black3;
      color: $white
    }
    &:last-child {
      margin-right: 0
    }
  }
}

@media (max-width: 1023px) {
  .news-page {
    .index__news-list {
      margin-left: 0;
      margin-right: 0;
    }
    .index__news-item {
      width: 48%;
    }
  }
}

@media (max-width: 940px) {
  .news-page {
    .index__news-item {
      width: 32%;
    }
  }
}

@media (max-width: 800px) {
  .news-page {
    .index__news-item {
      width: 48%;
    }
  }
}