.video-channel {
  &__list {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    margin: -10px;
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: calc(33.333% - 20px);
    min-width: 240px;
    background: $black2;
    padding: 10px;
    margin: 10px;
    border-radius: $borderRadius4;
    opacity: .8;
    &:hover {
      opacity: 1;
      h3 {
        color: $white;
      }
    }
  }
  &__link {
    h3 {
      padding: 0;
      min-height: auto;
      margin-bottom: 8px;
    }
    img {
      max-width: 200px;
    }
  }
  &__stat,
  &__stat div {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    color: $grey2;
    div {
      width: 90px;
      flex-direction: column;
      align-items: flex-start;
    }
    span {
      margin: 0 8px;
    }
    img {
      width: 20px;
      margin-right: 4px;
    }
  }
}