.mems-page {
  &__category {
    input:checked + label {
      background: $black3;
      color: $white;
      svg path {
        fill: $white;
      }
    }
    label {
      margin-right: 8px;
      margin-bottom: 8px;
    }
  }
  &__best {
    display: flex;
    justify-content: space-between;
    padding: 20px;
    background: $black2;
    border-radius: $borderRadius4;
    margin-top: 24px;
    a {
      display: flex;
      flex-direction: column;
      position: relative;
      width: 49%;
      height: 280px;
      overflow: hidden;
      background: $black2;
      border-radius: $borderRadius4;
      &:hover div {
        color: $white;
      }
    }
    div {
      padding: 7px 20px;
      width: 100%;
      background: $black3;
    }
    img {
      max-width: 100%;
    }
    span {
      display: flex;
      align-items: center;
      position: absolute;
      top: 44px;
      right: 10px;
      padding: 4px 10px;
      background: $black1;
      color: $grey2;
      border-radius: $borderRadius4;
      img {
        margin-right: 5px;
      }
    }
  }
  &__mem {
    margin-top: 1px;
    padding-top: 15px;
    padding-bottom: 15px;
    &-img {
      & > img {
        max-width: 100%;
      }
    }
    .forum-theme__top-block-wrap {
      margin-bottom: 0;
    }
  }
  &__mem-right-block {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    padding: 15px 0;
    img {
      width: auto;
      margin: 0;
    }
    &-time {
      font-size: 20px;
    }
    &-wrap {
      margin-top: 10px;
      display: flex;
      align-items: flex-start;
      justify-content: center;
      width: 100%;
    }
    & .mems-page__mem-right-block-img {
      width: 100%;
      max-width: 70px;
      max-height: 70px;
      margin-right: 10px;
    }
    &-stat {
      div {
        display: flex;
        align-items: center;
        white-space: nowrap;
        color: $grey2;
        font-size: 12px;
        margin-top: 5px;
        &:first-child {
          margin-top: 0;
        }
        span {
          margin-left: 5px;
        }
      }
    }
    &-name {
      font-size: 18px;
      margin-top: 10px;
      &:hover {
        color: $white;
      }
    }
  }
  &__page-mem {
    position: relative;
    display: flex;
    justify-content: center;
    margin-top: 24px;
    img {
      max-width: 100%;
    }
  }
}

@media (max-width: 650px) {
  .mems-page {
    &__best {
      flex-direction: column;
      a {
        width: 100%;
      }
    }
  }
}