.news-news {
  &__main-footer {
    margin-top: 24px;
    ul {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      margin: 0;
      li {
        margin-right: 8px;
        margin-bottom: 8px;
        &:last-child {
          margin-bottom: 8px;
        }
        a {
          padding: 0 12px;
          p {
            margin-left: 4px;
          }
          svg {
            margin-right: 4px;
          }
        }
        .btn-global--social {
          padding: 0;
          width: 32px;
        }
      }
    }
    &-wrap {
      display: flex;
      justify-content: space-between;
      margin-top: 24px;
      margin-bottom: 12px;
    }
    &-like {
      display: flex;
      flex-wrap: nowrap;
      margin-top: 8px;
      margin-bottom: 8px;
      li {
        a {
          svg {
            margin-right: 4px;
          }
          &:hover {
            color: $green;
            svg path {
              fill: transparent;
              stroke: $green;
            }
          }
        }
        &:last-child {
          margin-left: 8px;
          a {
            svg {
              transform: scale(1, -1);
            }
            &:hover {
              color: $red;
              svg path {
                stroke: $red;
              }
            }
          }
        }
      }
    }
  }
  &__news-list li {
    width: 32%;
  }
  & .matches__top,
  & .news-news__news-list {
    margin-left: 15px;
    margin-right: 15px;
  }
}

@media (max-width: 680px) {
  .news-news {
    &__main-footer {
      &-wrap {
        flex-direction: column;
      }
    }
    &__news-list {
      .index__news-item {
        width: 48%;
        &:nth-child(3) {
          display: none;
        }
      }
    }
  }
}