.forum-profile {
  &__head {
    display: flex;
    align-items: flex-start;
    margin-top: 24px;
    &-img {
      width: 150px;
      border-radius: $borderRadius4;
    }
    &-block {
      margin-left: 30px;
      display: flex;
      flex-direction: column;
      width: calc(100% - 180px);
      min-height: 150px;
    }
    &-title {
      margin: 0;
    }
    &-rank {
      font-size: 16px;
      font-weight: 500;
      color: $white;
    }
    &-status {
      color: $grey3
    }
    &-list {
      display: flex;
      justify-content: space-between;
      width: 100%;
      margin-top: auto;
      &--mob {
        display: none;
      }
    }
    &-item {
      &-title {
        color: $grey2;
        text-transform: uppercase;
        font-size: 13px;
      }
      &-text {
        font-size: 13px;
      }
    }
  }
  &__content {
    display: flex;
    margin-top: 24px;
    &-block {
      display: none;
      background: $black1;
      padding: 20px;
      padding-bottom: 24px;
      border-radius: 0 0 $borderRadius4 $borderRadius4;
      .global-content__comment {
        margin: 24px 0;
      }
      .title-global {
        margin: 0;
      }
      .global-content__btn-block {
        margin: 0;
        margin-bottom: 24px;
      }
      &-active {
        margin: 14px 0;
        &-block {
          width: 100%;
          display: flex;
          align-items: flex-start;
          padding: 10px 0;
        }
        &-img {
          max-width: 40px;
          max-height: 40px;
          display: flex;
          align-items: center;
          justify-content: center;
          margin-right: 10px;
        }
        &-theme {
          color: $grey2;
          a {
            color: $grey3;
            text-decoration: underline;
            &:hover {
              color: $white;
              text-decoration: none;
            }
          }
        }
        &-post {
          margin: 5px 0;
        }
        &-info {
          display: flex;
          align-items: center;
          span {
            display: flex;
            align-items: center;
            margin-right: 10px;
            color: $grey2;
            font-size: 12px;
            svg,
            img {
              margin-right: 5px;
            }
          }
        }
      }
    }
  }
  &__right-bar {
    margin-left: 24px;
    width: 100%;
    &-stat {
      display: flex;
      align-items: center;
      padding: 0 20px;
      margin: 8px 0 ;
      span {
        margin-left: 10px;
      }
    }
    &-mmr {
      padding: 20px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      text-transform: uppercase;
      div {
        font-size: 13px;
        color: $grey2;
      }
      span {
        font-weight: 700;
        font-size: 20px;
      }
    }
    &-subscr {
      .subtitle-global {
        display: flex;
        justify-content: space-between;
        align-items: center;
        span {
          font-size: 12px;
          color: $grey2;
        }
      }
      .component-block__list {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        padding: 10px 20px;
        margin: -7px;
        li {
          margin: 7px;
          a {
            display: flex;
            align-items: center;
            justify-content: center;
            max-width: 40px;
            max-height: 40px;
            img {
              width: 100%;
            }
          }
        }
      }
      &-last {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        padding: 10px 20px;
        text-align: center;
        font-size: 12px;
        &:hover {
          color: $white;
        }
      }
    }
  }
  &__topblock {
    display: flex;
    background: $black1;
    border-radius: $borderRadius4 $borderRadius4 0 0;
    overflow: hidden;
    &-item {
      width: 25%;
    }
    &-btn {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 56px;
      background: $black2;
      font-weight: 500;
      &:hover {
        background: transparent;
        color: $white;
      }
    }
  }
  &__warning {
    &-item {
      position: relative;
      padding: 10px 0;
      padding-right: 45px;
      border-bottom: 1px solid $black0;
    }
    &-top {
      font-weight: 700;
    }
    &-text {

    }
    &-info {
      margin-top: 10px;
      font-size: 13px;
      a {
        margin: 0 4px;
        &:hover {
          color: $white;
        }
      }
      span {
        margin-left: 4px;
        color: $grey2;
        svg, img {
          vertical-align: middle;
        }
      }
    }
    &-point {
      position: absolute;
      top: calc(50% - 15px);
      right: 0;
      padding: 5px 10px;
      background: $black2;
      font-weight: 700;
    }
  }
}

#forum-radio_1:checked ~ .forum-profile__topblock-wrap label[for="forum-radio_1"],
#forum-radio_2:checked ~ .forum-profile__topblock-wrap label[for="forum-radio_2"],
#forum-radio_3:checked ~ .forum-profile__topblock-wrap label[for="forum-radio_3"],
#forum-radio_4:checked ~ .forum-profile__topblock-wrap label[for="forum-radio_4"] {
  background: $black1;
  color: $white;
}

#forum-radio_1:checked ~ .forum-profile__content-block_1,
#forum-radio_2:checked ~ .forum-profile__content-block_2,
#forum-radio_3:checked ~ .forum-profile__content-block_3,
#forum-radio_4:checked ~ .forum-profile__content-block_4 {
  display: block;
}

@media (max-width: 1023px) {
  .forum-profile {
    &__content {
      flex-direction: column-reverse;
    }
    &__right-bar {
      margin-left: 0;
    }
    &__head {
      &-img {
        width: 100px;
      }
      &-block {
        margin-left: 10px;
        width: calc(100% - 100px);
      }
      &-title {
        font-size: 20px;
      }
      &-rank {
        font-size: 15px;
      }
      &-list {
        display: none;
        &--mob {
          margin: -10px;
          margin-top: 14px;
          display: flex;
          flex-wrap: wrap;
        }
      }
      &-item {
        width: calc(33% - 20px);
        margin: 10px;
      }
    }
  }
}

@media (max-width: 600px) {
  .forum-profile {
    &__topblock {
      min-width: 555px;
      &-wrap {
        width: 100%;
        overflow-x: scroll;
      }
    }
    &__head-item {
      width: calc(50% - 20px);
    }
  }
}