.forum-page {
  &__head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 24px;
    h2 {
      margin-top: 0;
    }
  }
  &__section {
    background: $black1;
    border-radius: $borderRadius4;
    padding: 19px 32px;
    margin-top: 1px;
    &-0 .forum-page__list {
      padding-left: 0;
    }
    &-1 {
      background: linear-gradient(264.15deg, rgba(23, 23, 26, 0.1) 81.4%, rgba(160, 23, 16, 0.1) 100%), #17171A;
      .forum-page__item::before,
      .forum-page__item::after {
        background-image: linear-gradient(#D75822 25%, rgba(255,255,255,0) 0%);
      }
    }
    &-2 {
      background: linear-gradient(264.15deg, rgba(23, 23, 26, 0.1) 81.4%, rgba(242, 161, 29, 0.1) 100%), #17171A;
      .forum-page__item::before,
      .forum-page__item::after {
        background-image: linear-gradient(#F3A220 25%, rgba(255,255,255,0) 0%);
      }
    }
    &-3 {
      background: linear-gradient(264.15deg, rgba(23, 23, 26, 0.1) 81.4%, rgba(35, 70, 189, 0.1) 100%), #17171A;
      .forum-page__item::before,
      .forum-page__item::after {
        background-image: linear-gradient(#2346BD 25%, rgba(255,255,255,0) 0%);
      }
    }
    &-4 {
      background: linear-gradient(264.15deg, rgba(23, 23, 26, 0.1) 81.4%, rgba(28, 141, 27, 0.1) 100%), #17171A;
      .forum-page__item::before,
      .forum-page__item::after {
        background-image: linear-gradient(#43AB66 25%, rgba(255,255,255,0) 0%);
      }
    }
    &-5 {
      background: linear-gradient(264.15deg, rgba(23, 23, 26, 0.1) 81.4%, rgba(100, 27, 137, 0.1) 100%), #17171A;
      .forum-page__item::before,
      .forum-page__item::after {
        background-image: linear-gradient(#542BCA 25%, rgba(255,255,255,0) 0%);
      }
    }
  }
  &__title-block {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title {
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 20px;
    line-height: 140%;
    letter-spacing: 0.15px;
    color: $grey0;
    &:hover {
      color: #fff;
    }
    img {
      margin-right: 14px;
    }
  }
  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 32px;
    height: 32px;
    background: $black2;
    border-radius: $borderRadius4;
    cursor: pointer;
    &:hover {
      background: $black3;
      svg path {
        fill: $white;
      }
    }
  }
  &__list {
    display: none;
    padding-top: 14px;
    padding-left: 49px;
  }
  &__item {
    position: relative;
    &::before,
    &::after {
      content: '';
      position: absolute;
      background-size: 1px 4px;
      background-position: right;
      background-repeat: repeat-y;
    }
    &::before {
      top: 0;
      left: -32px;
      width: 1px;
      height: 100%;
    }
    &:last-child::before {
      height: 29px;
    }
    &::after {
      top: 28px;
      left: -14px;
      width: 1px;
      height: 16px;
      transform: rotate(90deg);
      transform-origin: 0% 0%;
    }
    &-title-block {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 7px 0;
      border-top: 1px solid $black0;
    }
    &-title {
      font-weight: 500;
      font-size: 17px;
      line-height: 135%;
      letter-spacing: 0.25px;
      color: $grey0;
      &:hover {
        color: #fff;
      }
    }
    &-info {
      display: flex;
      align-items: center;
    }
    &-stat {
      display: flex;
      align-items: center;
      span {
        font-size: 12px;
        line-height: 14px;
        display: flex;
        align-items: center;
        letter-spacing: 0.3px;
        color: $grey2;
        margin-left: 8px;
        img {
          margin-right: 2px;
        }
      }
    }
    &-avtar {
      display: flex;
      align-items: center;
      margin-left: 16px;
      &-img {
        display: flex;
        align-items: center;
        justify-content: center;
        max-width: 40px;
        max-height: 40px;
        border-radius: $borderRadius50;
        margin-right: 8px;
      }
      &-wrap {
        width: 173px;
      }
      &-theme {
        display: block;
        font-size: 13px;
        max-width: 173px;
        &:hover {
          color: $white;
        }
      }
      &-wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
      }
      &-name,
      &-wrapper span {
        font-size: 11px;
        line-height: 150%;
        display: flex;
        align-items: center;
        letter-spacing: 0.25px;
        color: $grey2;
      }
      &-name {
        display: block;
        font-weight: bold;
        margin-right: 4px;
        max-width: 75px;
        &:hover {
          color: $white;
        }
      }
    }
    &-list {
      li {
        margin-top: 6px;
        padding-bottom: 6px;
        &::before {
          background: $grey0;
        }
      }
      a {
        display: block;
        color: $grey0;
        &:hover {
          color: $white;
        }
      }
    }
  }
  &__online {
    background: $black1;
    border-radius: $borderRadius4;
    padding: 20px 32px;
    margin-top: 24px;
    &-title {
      margin: 0;
      margin-bottom: 10px;
    }
    &-all-list {
      display: flex;
      flex-wrap: wrap;
      margin-top: 10px;
      a {
        font-size: 12px;
        &:hover {
          color: $white;
        }
      }
    }
  }
  &__chat {
    &-head {
      display: flex;
      align-items: center;
      justify-content: center;
      background: $black2;
      padding: 5px 20px;
      a:hover {
        color: $white;
      }
    }
    &-messager {
      background: $black3;
      position: relative;
      height: 250px;
      margin-bottom: -16px;
      padding: 10px;
      overflow-y: scroll;
      overflow-x: hidden;
      font-size: 12px;
      border: $black3;
    }
    &-message {
      display: flex;
      align-items: center;
      margin-top: 10px;
      &-img {
        min-width: 40px;
        min-height: 40px;
        width: 40px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: $borderRadius50;
        overflow: hidden;
        margin-right: 8px;
      }
      &-wrap {
        background: $black2;
        padding: 5px 10px;
        border-radius: $borderRadius4;
        margin-right: 8px;
      }
      &-name {
        font-size: 12px;
        &:hover {
          color: $white;
        }
      }
      .forum-theme__item-dots {
        margin-left: 0;
        margin-right: 0;
      }
      &-time {
        font-size: 11px;
        color: $grey2;
      }
      &-text {
        margin-top: 4px;
      }
    }
    &-textarea textarea {
      height: 40px;
      background: $black1 !important;
    }
  }
}

.forum-page__input:checked ~ .forum-page__title-block .forum-page__btn {
  transform: rotate(180deg);
  background: $black3;
  svg path {
    fill: $white;
  }
}
.forum-page__input:checked ~ .forum-page__list {
  display: block;
}

@media (max-width: 900px) {
  .forum-page {
    &__section {
      padding: 15px;
    }
  }
}

@media (max-width: 800px) {
  .forum-page {
    &__section {
      padding: 15px;
    }
    &__item-stat {
      display: none;
    }
  }
}

@media (max-width: 600px) {
  .forum-page {
    &__item-title-block {
      padding: 16px 0;
      align-items: flex-start;
      flex-direction: column;
    }
    &__item-avtar {
      margin-top: 10px; 
      margin-left: 0;
    }
  }
}