.video-video {
  &__video {
    margin-top: 24px;
    height: 484px;
    border-radius: $borderRadius4;
  }
  &__list {
    .index__news-item {
      width: 32%;
    }
  }
}

@media (max-width: 680px) {
  .video-video__video {
    height: 230px;
  }
  .video-video__list {
    .index__news-item {
      width: 48%;
      &:last-child {
        display: none;
      }
    }
  }
}