.shop-sidebar {
  &__title:hover {
    color: $grey0;
  }
  &__wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    &--opacity:hover {
      opacity: .8;
    }
    span {
      position: absolute;
      top: 47%;
      left: 51%;
      transform-origin: 50% 50%;
      transform: translate(-50%, -50%);
      font-size: 30px;
      color: $white;

    }
  }
  &__pay {
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin: -7px;
    a {
      margin: 7px;
    }
    a:hover {
      opacity: .8;
    }
  }
}