.cybersport-matches-match {
  &__preview {
    background-image: $gradientMatch ,url(../img/cybersport-matches-match/img-bg.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: $borderRadius4;
    padding: 24px 0;
    margin-top: 24px;
    &-descr {
      text-align: center;
      text-transform: uppercase;
      &-name {
        font-size: 20px;
        &:hover {
          color: $white;
        }
      }
      &-text {
        font-size: 13px;
      }
    }
    &-teams {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: 15px;
      &-team {
        display: flex;
        align-items: center;
        width: 50%;
        &--right {
          flex-direction: row-reverse;
          text-align: right;
        }
      }
      &-img {
        margin: 0 15px;
        img {
          width: 100px;
        }
      }
      &-list a {
        margin: 0 5px;
        &:hover {
          color: $white;
        }
      }
      &-item {
        margin-bottom: 5px;
        &:first-child {
          margin-bottom: 13px;
          a {
            font-size: 20px;
          }
        }
      }
      &-score {
        width: 130px;
        position: absolute;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
        top: 43px;;
        left: calc(50% - 65px);
        font-size: 12px;
        color: $grey2;
        a {
          font-size: 30px;
          color: $grey1;
          &:hover {
            color: $white;
          }
        }
      }
    }
  }
  &__coeff {
    &-block {
      background: $black2;
      box-shadow: 0 0 10px $black0;
      border-radius: $borderRadius4;
      overflow: hidden;
    }
    &-title {
      background: $black3;
      color: $grey0;
      font-size: 20px;
      margin: 0;
      margin-bottom: 10px;
      padding: 13px 0;
      padding-left: 20px;
    }
    &-text {
      padding-left: 20px;
      font-size: 13px;
      color: $grey2;
      margin-bottom: 5px;
    }
    &-bookmaker {
      display: flex;
      justify-content:space-between;
      align-items: center;
      padding: 10px 20px;
      border-bottom: 1px solid $black1;
      &:last-child {
        border-bottom: none;
      }
      &-img {
        width: 100px;
      }
      &-wrap {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
      }
      &-team {
        display: flex;
        justify-content: center;
        width: 40%;
        text-align: left;
      }
      &-coeff {
        display: flex;
        justify-content: center;
        width: 100px;
        margin: 0 10px;
        span {
          margin: 0 5px;
        }
      }
      &-link {
        background: $black1;
      }
    }
  }
  &__record-game {
    &-toggels {
      display: flex;
      align-items: center;
    }
    &-toggel {
      display: inline-block;
      padding: 5px 14px;
      margin-right: 8px;
    }
    &-block {
      display: none;
    }
    &-none {
      margin-top: 20px;
    }
  }
}
#game_1:checked ~ .cybersport-matches-match__record-game-block--1,
#game_2:checked ~ .cybersport-matches-match__record-game-block--2,
#game_3:checked ~ .cybersport-matches-match__record-game-block--3 {
  display: block;
}

#game_1:checked ~ .cybersport-matches-match__record-game-toggel[for=game_1],
#game_2:checked ~ .cybersport-matches-match__record-game-toggel[for=game_2],
#game_3:checked ~ .cybersport-matches-match__record-game-toggel[for=game_3] {
  color: $white;
}

@media (max-width: 700px) {
  .cybersport-matches-match {
    &__preview {
      margin: 0 -15px;
      margin-top: 24px;
      &-teams {
        &-team {
          flex-direction: column;
          align-items: flex-start;
          margin-left: 15px;
          width: calc(50% - 15px);
          &--right {
            margin-right: 15px;
            margin-left: 0;
            align-items: flex-end;
          }
        }
        &-img {
          margin: 0 0 10px 0;
          img {
            width: 80px;
          }
        }
        &-score {
          top: 0;
        }
      }
    }
    &__coeff {
      &-title,
      &-text {
        padding-left: 15px;
      }
      &-bookmaker {
        flex-direction: column;
        flex-wrap: wrap;
        padding: 20px 15px;
        &-wrap {
          margin: 15px 0;
        }
      }
    }
    &__record-game {
      .global-content__tabel-version-1--cybersport {
      .global-content__tabel-version-1_caption {
          display: none;
        }
        tr th:nth-child(6),
        tr th:nth-child(7),
        tr th:nth-child(8),
        tr th:nth-child(9),
        tr td:nth-child(6),
        tr td:nth-child(7),
        tr td:nth-child(8),
        tr td:nth-child(9) {
          display: none;
        }
        tr th:nth-child(2) {
          text-align: left;
        }
      }
    }
  }
}

@media (max-width: 600px) {
  .cybersport-matches-match {
    &__coeff-block {
      margin: 0 -15px;
    }
  }
}