.blog-page {
  &__section {
    margin-top: 24px;
    background: $black2;
    border-radius: $borderRadius4;
  }
  &__info {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    padding: 0 20px;
    &-profile,
    &-stat {
      display: flex;
      align-items: center;
    }
    &-profile {
      margin: 10px 0;
      margin-right: 20px;
      img {
        margin-right: 8px;
      }
      &:hover {
        color: $white;
      }
    }
    &-stat {
      div {
        display: flex;
        align-items: center;
        margin-right: 15px;
        font-size: 13px;
        color: $grey2;
        img, svg {
          margin-right: 4px;
        }
      }
    }
  }
  &__post {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 10px 20px;
    padding-bottom: 20px;
    .text-clip-row {
      max-height: 136px;
    }
    .btn-global {
      background: $black3;
      margin-left: auto;
      margin-top: 14px;
      color: $grey1;
      &:hover {
        color: $white;
        background: $black4;
      }
    }
  }
}