.base-hero-stat {
  &__block-def,
  &__block-attr,
  &__block-attr-second {
    display: none;
  }
  &__block-input {
    margin-bottom: 14px;
    display: flex;
    align-items: center;
    width: 100%;
    margin-top: 24px;
    margin-bottom: 24px;
    border-radius: $borderRadius4;
    overflow: hidden;
    label {
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      width: 50%;
      height: 56px;
      background: $black2;
      cursor: pointer;
    }
  }
  .base-items__shop-link:hover ~ .base-items__shop-descr {
    display: flex;
  }
}

#collage-def:checked ~ .base-hero-stat__block-input label[for="collage-def"],
#collage-attr:checked ~ .base-hero-stat__block-input label[for="collage-attr"],
#collage-attr-second:checked ~ .base-hero-stat__block-input label[for="collage-attr-second"] {
  background: $black3;
}
#collage-def:checked ~ .base-hero-stat__block-def,
#collage-attr:checked ~ .base-hero-stat__block-attr,
#collage-attr-second:checked ~ .base-hero-stat__block-attr-second {
  display: flex;
}