.shop-page {
  &__list {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin: 0 -7px;
  }
  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: calc(33.333% - 14px);
    margin: 7px;
    padding: 10px;
    background: $black2;
    border-radius: $borderRadius4;
    &:hover {
      background: $black3;
    }
    & > span {
      position: absolute;
      top: 10px;
      right: 0;
      background: $gradientRed;
      border-radius: $borderRadius4 0 0 $borderRadius4;
      font-size: 12px;
      color: $white;
      padding: 2px 8px;
    }
  }
  &__img {
    height: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      width: 100%;
    }
  }
  &__name {
    min-height: 48px;
    text-align: center;
    margin-bottom: 5px;
    &:hover {
      color: $white;
    }
  }
  &__price {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    color: $green;
    font-size: 18px;
    margin: 0 5px;
    span {
      margin: 0 5px;
      font-size: 14px;
      color: $grey2;
      text-decoration: line-through;
    }
  }
  &__btn {
    background: $black0;
    margin-top: 10px;
    color: $grey1;
    font-size: 15px;
    text-align: center;
    height: auto;
    padding: 5px 15px;
    &:hover {
      background: $red;
    }
  }
}

@media (max-width: 700px) {
  .shop-page {
    &__item {
      width: calc(50% - 14px);
    }
  }
}