.shop-basket {
  margin-top: 24px;
  &__wrap {
    margin-top: 15px;
    &-radio {
      margin-top: 15px;
      div {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        label {
          display: flex;
          align-items: center;
          margin-right: 10px;
          & input {
            width: auto;
          }
        }
      }
    }
  }
  &__title {
    color: $green;
  }
  &__text {

  }
  &__text-small {
    font-size: 12px;
  }
  input,
  select {
    width: 280px;
    height: 40px;
    border: none;
    margin-right: 10px;
    &:hover {
      background: $black3;
    }
    &::placeholder {
      color: $grey2;
    }
  }
  input {
    padding: 5px 12px;
    background: $black2;
    border-radius: $borderRadius4;
    font-size: 13px;
    color: $grey1;
    &[type="submit"] {
      margin: auto;
      margin-top: 24px;
      &:hover {
        background: $red;
      }
    }
  }
  select {
    background: $black2;
    padding: 0 12px;
    font-size: 15px;
    line-height: 150%;
    letter-spacing: 0.5px;
    color: $grey2;
  }
}

@media (max-width: 600px) {
  .shop-basket {
    &__wrap {
      display: flex;
      flex-direction: column-reverse;
    }
    &__table {
      width: 100%;
      overflow-x: scroll;
      .global-content__tabel-version-1 {
        min-width: 555px;
      }
    }
  }
}