.base-hero-hero {
  &__wrap {
    .subtitle-global-text {
      display: flex;
      margin-top: 24px;
      margin-bottom: 14px;
    }
    a.subtitle-global-text:hover {
      color: $white;
    }
    p {
      margin-bottom: 8px;
    }
  }
  &__descr {
    display: flex;
    flex-direction: column;
    margin-top: 24px;
    &-text {
      font-size: 14px;
      margin-bottom: 24px;
      a {
        color: $grey2;
        text-decoration: underline;
        &:hover {
          color: $white;
        }
        img {
          width: 16px;
          vertical-align: middle;
          margin-right: 4px;
          margin-bottom: 2px;
        }
      }
    }
    &-main {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      iframe {
        display: flex;
        max-width: 380px;
        margin: auto;
      }
      &-wrapper {
        width: 50%;
        iframe {
          margin-bottom: 20px;
        }
      }
      .global-content__tabel-version-1 {
        min-width: 380px;
        font-size: 13px;
        margin-bottom: 20px;
        & td > div,
        & th > div {
          padding: 5px;
        } 
      }
    }
  }
  &__skill {
    padding: 15px;
    background: $black2;
    border-radius: $borderRadius4;
    margin-top: 2px;
    .base-items-item__note {
      background: $black1;
      margin-bottom: 8px;
    }
    &-title {
      width: 100%;
      font-size: 20px;
      color: $grey0;
      font-weight: bold;
      text-align: center;
    }
    &-img {
      margin-top: 10px;
      display: flex;
      align-items: flex-start;
      img {
        width: 96px;
        margin-right: 10px;
        border-radius: $borderRadius4;
      }
    }
    &-manacost {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      align-items: center;
      background: $black3;
      border-radius: $borderRadius4;
      padding: 0 10px;
      margin: 10px 0;
      div {
        display: flex;
        align-items: center;
        margin: 10px 0;
      }
      img {
        width: 20px;
        margin-right: 8px;
      }
    }
  }
  &__guid {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: -10px;
    margin-top: 14px;
    &-item {
      margin: 10px;
      width: calc(50% - 20px);
    }
    &-link {
      display: flex;
      flex-direction: column;
      width: 100%;
      padding: 10px;
      background: $black2;
      border-radius: $borderRadius4;
      overflow: hidden;
      &:hover {
        background: $black3;
      }
    }
    &-top {
      position: relative;
      display: flex;
      align-items: flex-start;
      margin-bottom: 8px;
      img {
        margin-right: 10px;
        max-width: 100px;
        border-radius: $borderRadius4;
      }
      &-patch {
        position: absolute;
        font-size: 10px;
        padding: 2px 5px;
        background: $black1;
        border-radius: $borderRadius4;
      }
    }
    &-bottom {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      span {
        display: flex;
        align-items: center;
        color: $grey2;
        font-size: 12px;
        margin-right: 8px;
        img {
          margin-right: 4px;
        }
      }
    }
  }
}

@media (max-width: 800px) {
  .base-hero-hero {
    &__skill-img img {
      width: 70px;
    }
    &__guid-item {
      width: 100%;
    }
    &__descr-main-wrapper {
      width: 100%;
      display: flex;
      flex-direction: column-reverse;
      iframe {
        margin-bottom: 0;
      }
    }
    &__descr-table {
      width: 100%;
      overflow-x: scroll;
    }
  }
}


@media (max-width: 600px) {
  .base-hero-hero {
    &__skill,
    &__guid-link {
      margin: 0 -15px;
    }
    &__guid-link {
      width: calc(100% + 30px);
    }
  }
}