.cybersport-players-player {
  display: flex;
  align-items: flex-end;
  flex-wrap: wrap;
  margin-top: 24px;
  margin-bottom: 24px;
  &__social {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    flex-wrap: wrap;
    margin-bottom: -8px;
    li {
      margin-left: 8px;
      margin-bottom: 8px;
    }
  }
  &__img {
    width: 120px;
    padding: 10px;
    padding-left: 0;
    margin-right: 20px;
  }
  &__table {
    font-size: 13px;
    span {
      color: $grey2;
    }
    td {
      padding-right: 10px;
    }
  }
  &__top-block {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    &-1 {
      width: 49%;
      display: flex;
      flex-direction: column;
      table {
        margin: 0;
        margin-bottom: 20px;
      }
      &>div {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        background: $black2;
        padding: 10px;
        &>a {
          display: flex;
          align-items: center;
          img {
            width: 70px;
            margin-right: 8px;
          }
        }
        span {
          color: $grey2;
        }
      }
      h3 {
        padding: 15px 10px;
        margin: 0;
      }
    }
  }
  &__teams {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    margin: -10px;
    margin-bottom: 24px;
    &-block {
      position: relative;
      display: flex;
      flex-direction: column;
      width: 146px;
      height: 200px;
      margin: 10px;
      background: $black2;
      padding: 10px;
      &:hover {
        .cybersport-players-player__teams-list {
          display: block;
        }
      }
    }
    &-title {
      position: absolute;
      z-index: 2;
      &:hover {
        color: $white;
      }
      img {
        margin-right: 5px;
      }
    }
    &-img {
      display: flex;
      align-items: center;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      img {
        width: 100%;
      }
    }
    &-list {
      font-size: 13px;
      position: absolute;
      top: 0;
      left: 0;
      margin: 0 !important;
      width: 100%;
      height: 100%;
      padding-top: 45px !important;
      padding-left: 10px !important;
      background: rgba($black2, .9);
      display: none;
      a {
        color: $grey1!important;
      }
    }
  }
}

@media (max-width: 600px) {
  .cybersport-players-player {
    &__top-block {
      // margin: 0 -15px;
    }
    &__top-block-1 {
      width: 100%;
    }
  }
}