.base-hero {
  &__subtitle {
    background: $black2;
    border-radius: $borderRadius4;
    &:hover {
      color: $grey0;
    }
    img {
      width: 33px;
      border-radius:$borderRadius50;
      margin-right: 8px;
    }
  }
  &__block {
    margin-top: 24px;
  }
  &__block-hero {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin: 0 -4px;
  }
  &__link-hero {
    position: relative;
    display: flex;
    width: 83px;
    height: 50px;
    margin: 0 4px;
    margin-top: 8px;
    opacity: 0.8;
    img {
      width: 100%;
    }
    &-name {
      display: none;
    }
    &:hover {
      opacity: 1;
      & .base-hero__link-hero-name {
        position: absolute;
        top: 100%;
        left: calc(50% - 60px);
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        background: $black2;
        border-radius: $borderRadius4;
        padding: 9px 10px;
        width: 120px;
        z-index: 2;
      }
    }
  }
}