.cybersport-matches {
  &__top-block {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    &-link {
      margin-right: 10px;
      margin-bottom: 5px;
      opacity: .8;
      &:hover {
        opacity: 1;
      }
    }
    .btn-global {
      width: 190px;
      margin-left: auto;
      img,svg {
        margin-right: 6px;
      }
    }
  }
  &__matches {
    margin-top: 24px;
    border-radius: $borderRadius4;
    &>div:first-child {
      border-top-left-radius: $borderRadius4;
      border-top-right-radius: $borderRadius4;
    }
    &>div:last-child {
      border-bottom-left-radius: $borderRadius4;
      border-bottom-right-radius: $borderRadius4;
    }
    &--map {
      .cybersport-matches__matches-match {
        padding: 20px 100px;
      }
      .cybersport-matches__matches-left {
        left: 20px;
      }
      .cybersport-matches__matches-right {
        right: 20px;
      }
    }
    &-section-descr {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 10px;
      border-bottom: 1px solid $black1;
      span {
        text-transform: uppercase;
        font-size: 13px;
      }
      a {
        position: absolute;
        right: 0;
        font-size: 13px;
        color: $grey2;
        text-transform: uppercase;
        &:hover {
          color: $white;
        }
      }
    }
    &-match {
      position: relative;
      display: flex;
      align-items: center;
      width: 100%;
      padding: 10px;
      border-bottom: 1px solid $black1;
      background: $black2;
      &:hover {
        background: $gradientHover;
      }
      &:last-child {
        border: none;
      }
      &-wrap {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: auto;
      }
    }
    &-left,
    &-right {
      position: absolute;
      display: flex;
      font-size: 12px;
      opacity: .7;
    }
    &-left {
      left: 10px;
      img {
        width: 76px;
      }
      div {
        display: none;
        position: absolute;
        top: 100%;
        left: 0;
        padding: 4px;
        width: 150px;
        background: $black3;
        z-index: 2
      }
      &:hover {
        opacity: 1;
        div {
          display: block;
        }
      }
    }
    &-right {
      right: 10px;
    }
    a.cybersport-matches__matches-right:hover {
      opacity: 1;
    }
    &-time {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 70px;
      font-size: 15px;
      margin: 0 10px;
      text-align: center;
      span {
        font-size: 11px;
        color: $grey2;
        font-weight: 400;
      }
    }
    &-hero {
      display: flex;
      justify-content: center;
      align-items: center;
      opacity: 0.8;
      img {
        max-width: 42px;
      }
      &:hover {
        opacity: 1;
      }
    }
    &-block {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-wrap: wrap;
      &-wrap {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
      }
    }
    a.cybersport-matches__matches-block:hover {
      color: $white;
    }
    &-team {
      margin: 0 12px;
    }
    &-name {
      font-size: 13px;
    }
    &-coeff {
      font-size: 10px;
    }
  }
}

@media (max-width: 650px) {
  .cybersport-matches {
    &__matches {
      &--map {
        .cybersport-matches__matches-match {
          padding: 10px;
        }
        .cybersport-matches__matches-left {
          left: auto;
        }
      }
      &-section-descr {
        justify-content: space-between;
        a {
          position: relative;
        }
      }
      &-match {
        display: flex;
        flex-direction: column;
      }
      &-left {
        position: relative;
        left: auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 10px 0;
        width: 100%;
        div {
          display: block;
          position: relative;
          background: transparent;
        }
      }
      &-right {
        top: 10px;
      }
      &-block {
        flex-direction: column;
        &-left {
          flex-direction: column-reverse;
        }
        &--hero {
          flex-direction: row;
        }
      }
    }
  }
}

@media (max-width: 600px) {
  .cybersport-matches {
    &__matches,
    &__matches-blockatches {
      margin-left: -15px;
      margin-right: -15px;
    }
  }
}