.matches-sidebar {
  .matches__slide {
    width: auto;
    display: flex;
    background: $black2;
    border-top: 1px solid $black3;
    &--live::before {
      background-image: $gradientMatchLive;
    }
  }
}

@media (max-width: 1023px) {
  .matches-sidebar {
    .matches__slide:nth-child(2),
    .matches__slide:nth-child(6) {
      display: none;
    }
  }
}