.base-items-item {
  &__wrap {
    margin-bottom: 24px;
  }
  &__item-block {
    display: flex;
    position: relative;
    width: auto;
    background: transparent;
    padding: 0;
    margin-top: 24px;
  }
  &__note,
  &__show-all {
    margin-top: 20px;
    width: 150px;
    &-block {
      display: none;
      padding: 15px 0;
    }
  }
  &__show-all-block {
    padding: 0;
  }
  .text-global-ctrl {
    margin-top: 24px;
    margin-bottom: 10px;
  }
  &__change-block {
    margin-top: 24px;
    div {
      margin-top: 15px;
    }
  }
}

#note:checked ~ .base-items-item__note {
  background: $black3;
  color: $white;
  &-block {
    display: block;
  }
}

#show-all:checked ~ .base-items-item__show-all {
  display: none;
  &-block {
    display: block;
  }
}